import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";
import React, { useEffect, useState } from "react";




const Head = () =>{
  return(
    <section className="hero is-link is-light is-medium">
    <div className="hero-body">
      <div className="container">
        <h1 className="title">
          Web Page Dayo
        </h1>
        <h2 className="subtitle">
          Minori
        </h2>
      </div>
    </div>
  </section>
  );
}




const Introduction = () =>{
  return (
  <body>
    <section className="section">
      <div className="container">
        <h1 className="title">自己紹介</h1>
        <h2 className="subtitle">
          <li>名前：近藤みのり</li>
          <li>出身地：埼玉</li>
          <li>趣味：ピアノ、コピーダンス</li>
          <li>マイブーム：スプラトゥーン</li>
        </h2>
      </div>
    </section>
  </body>
  );
}



const Tech = () =>{
  return (
  <body>
    <section className="section">
      <div className="container">
        趣味でピアノで耳コピをよくするので、手順とコツを自分なりにまとめてみました。
        <br /><br />
        <span className="tag is-link is-light">Step1</span>
        <h1 className="title">メロディー</h1>
        <h2 className="subtitle">
          どんな曲でも、まずはメロディーをつかむことから始めます！原曲を聞いてメロディーを
          弾きます。どうしてもわからないときはYoutubeであれば再生速度を変えることができるので
          遅くして聞き取ったり、イヤホンをしてより音に集中する環境を作ったりします。
          ここではハモリなどは無視してコピーしていきましょう。
        </h2>
        <span className="tag is-link is-light">Step2</span>
        <h1 className="title">簡単な伴奏</h1>
        <h2 className="subtitle">
          曲を聞くときに、ベースの音を意識して聞いてみましょう。慣れていないうちは
          なかなか難しいかもしれません。楽器構成が単純な曲（少ない楽器で構成されている曲）
          だと聞き取りやすいことが多いので最初はわかりやすい曲からチャレンジしてみることを
          お勧めします。ベースが聞き取れなくても1音1音メロディーと合わせて伴奏を
          弾いてみて探っていきましょう。ベースも最初は複雑な和音は使わず、簡単な1音を
          オクターブで交互に弾くというような伴奏をします。
        </h2>
        <span className="tag is-link is-light">Step3</span>
        <h1 className="title">アレンジ</h1>
        <h2 className="subtitle">
          最後にアレンジです。ここまでメロディーと伴奏をつけました。これに装飾を加えます。
          <li>メロディー：ところどころ音を加えて和音にしてみる、ハモリの部分を弾く</li>
          <li>伴奏：メロディー同様和音にしてみたり曲調に応じてリズムを変えてみる</li>
          こんな感じです。
        </h2>
        <br /><br />
        <span className="tag is-danger is-light">Plus α1</span>
        <h1 className="title">伴奏がカギ</h1>
        <h2 className="subtitle">
          曲調は伴奏の仕方で大きく変わります。バラードの時は静かに、明るい曲の時は
          元気よく、ダイナミックな感じにするとその曲調を再現できると思います。
          リズム感を出したいときは裏拍を取ってみたりと伴奏ひとつで曲調ががらりと
          変わります。
        </h2>
        <span className="tag is-danger is-light">Plus α2</span>
        <h1 className="title">裏技</h1>
        <h2 className="subtitle">
          元の曲から聞き取ってピアノで再現するのは難しいです。そこで少し反則かも
          しれませんが、耳コピが得意な人の動画を聞くとどう再現すればいいか
          大きなヒントになります。特に手元を移している動画を見て、どの音を
          触っているのか解読するのが一番の近道です。
        </h2>
      </div>
    </section>
  </body>  
  );
}


const Image = ({ url }) => {
  return (
    <div className="card">
      <div className="card-image">
        <figure className="image">
          <img alt="pokemon" src={url} />
        </figure>
      </div>
    </div>
  );
};



const Content = ({ data }) => {
  if (data == null) {
    return (
      <div className="content">
        <p>loading...</p>
      </div>
    );
  }
  return (
    <div className="columns is-vcentered is-multiline">
      {data.cards.map(({imageUrl}, i) => {
        return (
          <div key={i} className="column is-3">
            <Image url={imageUrl} />
          </div>
        );
      })}
    </div>
  );
};





const Gallery = () =>{
  const [data, setData] = useState(null);
  useEffect(() => {
    fetch("https://api.pokemontcg.io/v1/cards?setCode=det1")
      .then((response) => response.json())
      .then((data) => {
        setData(data);
        // console.log(data);
      });
  }, []);
  
  return (
  <body>
    <section className="section">
      <div className="container">
        <h1 className="title">ポケモン</h1>
        <h2 className="subtitle">
          名探偵ピカチュウのポケモンカード。
          引用元は{""}<a href="https://pokemontcg.io/">こちら</a>
        </h2>
      </div>
    </section>
    <section className="section">
      <div className="container">
        <Content data={data} />
      </div>
    </section>
  </body>  
  );
}



const Body =() =>{
  return (
    <Router>
      <div className="container">
        
      <div className="tabs is-medium">
        <ul>
        <li className="is-active">
          <Link to="/">自己紹介</Link>
        </li>
        <li>
          <Link to="/tech">耳コピ(ピアノ)</Link>
        </li>
        <li>
          <Link to="/gallery">ポケモン</Link>
        </li>
        </ul>  
      </div>
      <Switch>
        <Route path="/tech">
          <Tech />
        </Route>
        <Route path="/gallery">
          <Gallery />
        </Route>
        <Route path="/">
          <Introduction />
        </Route>
      </Switch>
      </div>
    </Router>
  )
}




const Bottom = () =>{
    return(
        <section className="hero is-link is-light">
  <div className="hero-body">
    <div className="container">
      &copy;minori
    </div>
  </div>
</section>
    )
}


const App = () =>{
    return (
        <div>
            <Head />
            {/* <div className="container"> */}
              <Body />
            {/* </div> */}
            <Bottom />
        </div>
    );
};

export default App;